require('../scss/index.scss');

// form backend
// sort google map
// add correct number to topnav and nav mobile
// mobile find us map selector arrows pointing down

const addressCoventry = "https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2432.89696291629!2d-1.5123615982534382!3d52.42666462969756!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x48774b8e2b3815cf%3A0x87afb656aaf9b755!2sCoventry%20CV6%203LT!5e0!3m2!1sen!2suk!4v1637693272559!5m2!1sen!2suk";
const addressKidderminster = "https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2436.530003591144!2d-2.267274048255346!3d52.360809079685126!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x48708c9880a72a13%3A0x9bb9c8dc30d29778!2sForge%20House%2C%20Stourport%20Rd%2C%20Kidderminster%20DY11%207QE!5e0!3m2!1sen!2suk!4v1637694034821!5m2!1sen!2suk";

window.addEventListener('load', ()=>{
  const map = document.getElementById('map-loader');
  const mapCoventry = document.getElementById('map-load-coventry');
  const mapKidderminster = document.getElementById('map-load-kidderminster');
  let activeMap = "coventry";

  const changemap = () => {
    map.setAttribute('src', activeMap==="coventry" ? addressCoventry : addressKidderminster);
  }

  mapCoventry.addEventListener('click', ()=>{
    activeMap = "coventry";
    mapCoventry.classList.add('active');
    mapKidderminster.classList.remove('active');
    changemap();
  });
  mapKidderminster.addEventListener('click', ()=>{
    activeMap = "kidderminster";
    mapKidderminster.classList.add('active');
    mapCoventry.classList.remove('active');
    changemap();
  }); 

  const navtoggle = document.getElementById('toggle-nav');
  const nav = document.getElementById('navigation');
  let opened = false;
  navtoggle.addEventListener('click', ()=>{
    opened = !opened;
    opened ? nav.classList.add('active') : nav.classList.remove('active');
    opened ? navtoggle.classList.add('active') : navtoggle.classList.remove('active');
  })
})